.navbar {
  width: 100%;
  height: 60px;
  background-color: rgb(19, 19, 19);
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.links {
  margin-right: 50px;
  display: flex;
  align-items: center;
}

.links a {
  text-decoration: none;
  color: white;
  margin-left: 20px;
  font-size: 15px;
  padding: 5px 10px;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.links a:hover {
  background-color: #555;
}

/* Add specific styles for the "Profile" link */
.links a:last-child {
  background-color: #007bff;
}

.links a:nth-child(4) {
  background-color: #007bff;
}

.links a:last-child {
  background-color: #007bff;
}
